'use strict';

require('fecomponent/mobi-zepto-countdown');
require('fecomponent/mobi-jswebview');
var Wxshare = require('components/wxshare/wxshare');
var Dialog = require('components/dialog/dialog');
var globalConfig = require('fecomponent/mobi-detect-ua');

var CommonwealPage = function() {};

CommonwealPage.prototype.countDown119 = function() {
  var $countDown = $('.j_CountDown');
  var serverTime = parseInt($countDown.attr('data-servertime') || Date.now());
  var endTime = parseInt($countDown.attr('data-endtime'));

  var counter = new $.countdown({
    correctDateOffset: parseInt((serverTime - Date.now()) / 1000),
    endDate: endTime / 1000,
    onUpdate: function(arr) {
      var d = arr.days;
      var h = arr.hours;
      var m = arr.minutes;

      $countDown.find('.j_CountDay').html(d);
      $countDown.find('.j_CountHour').html(h);
      $countDown.find('.j_CountMin').html(m);
    }
  });
  counter.start();
};

CommonwealPage.prototype.pageShare = function() {
  var shareData = {
    title: '达人店11·9总动员狂欢节',
    desc: '达人店11·9总动员狂欢节来啦，万店齐发，买贵包赔！！万件好货，最低1折 ！！！',
    link: window.location.href,
    imgUrl: 'https://cdn1.showjoy.com/images/81/8162111445d14d409312fed3cb086767.jpg'
  };
  Wxshare(shareData);

  if (globalConfig.Browser.versions.showjoyShopiOs || globalConfig.Browser.versions.showjoyShopAndroid) {
    $.JSBridge.registerHandler('wxShareInfo', function() {
      return shareData;
    });

    $.JSBridge.callHandler('shopc_send_shareInfo', shareData, function() {});

    $('.j_Share').on('click', function() {
      $.JSBridge.callHandler('c_share', shareData, function() {});
    });
  } else {
    $('.j_Share').on('click', function() {
      Dialog.wxShare();
    });
  }
};

CommonwealPage.prototype.eventBind = function() {
  $('.j_SignUp').on('click', function() {
    if ($(this).attr('data-openshop') === 'false') {
      Dialog.toast('对不起，公益战队队长招募活动只有付费店主可以参加哦！');
    } else if ($(this).attr('data-hasenrolled') === 'true') {
      $('.j_Title').html('您已组建战队！');
      $('.j_SuccessCover').removeClass('hide').addClass('see');
    } else {
      $('.j_SignUpCover').removeClass('hide').addClass('see');
    }
  });

  $('.j_CloseBtn').on('click', function() {
    $('.j_SignUpCover').removeClass('see').addClass('un-see');
    setTimeout(function() {
      $('.j_SignUpCover').addClass('hide').removeClass('un-see');
    }, 300);
  });

  $('.j_SuccessCloseBtn').on('click', function() {
    $('.j_SuccessCover').removeClass('see').addClass('un-see');
    setTimeout(function() {
      $('.j_SuccessCover').addClass('hide').removeClass('un-see');
    }, 300);
  });

  $('.j_Invite').on('click', function() {
    window.location.href = $(this).attr('data-href');
  });

  $('.j_ConfirmBtn').on('click', function() {
    var $this = $(this);
    if (!$this.hasClass('disabled')) {
      var teamName = $('.j_TeamName').val().trim();

      if (teamName.length === 0) {
        Dialog.toast('战队名不能为空~');
      } else if (teamName.length > 10) {
        Dialog.toast('战队名不能超过10个字~');
      } else {
        Dialog.toast('数据请求中…', 1000);
        $this.addClass('disabled');
        $.ajax({
          url: '/api/shop/enter119',
          type: 'GET',
          data: {
            teamName: teamName
          },
          dataType: 'json',
          success: function(result) {
            $this.removeClass('disabled');
            if (result.isSuccess) {
              $('.j_Title').html('恭喜您！组建战队成功！');
              $('.j_SignUpCover').removeClass('see').addClass('un-see');
              setTimeout(function() {
                $('.j_SignUpCover').addClass('hide').removeClass('un-see');
              }, 300);
              $('.j_SuccessCover').removeClass('hide').addClass('see');
            } else {
              Dialog.toast(result.msg);
            }
          }
        });
      }
    }
  });
};

CommonwealPage.prototype.init = function() {
  this.countDown119();
  this.pageShare();
  this.eventBind();
};

$(document).ready(function() {
  new CommonwealPage().init();
});



